import OrderDetailProducts from "./OrderDetailProducts";
import OrderDetailInfo from "./OrderDetailInfo";
import { Link, useNavigate, useParams } from "react-router-dom";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, deleteOrder, deliverOrder, getOrderDetails } from "../../actions/orderAction";
import { UPDATE_ORDER_RESET } from "../../constants/orderConstants";
import { useAlert } from "react-alert";
const OrderDetailmain = () => {

  const { id } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const alert = useAlert();
  const orderDetails = useSelector((state) => state.orderDetails);
  const { order, orderItems, shippingInfo, loading, paymentInfo, error, user } = orderDetails;
  const orderDeliver = useSelector((state) => state.orderDeliver);
  const { error: updateError, loading: loadingDelivered, success: successDelivered } = orderDeliver;



  // const deliverHandler = () => {
  //   dispatch(deliverOrder(order));
  // };
  const deliverHandler = (e) => {
    e.preventDefault();

    const myForm = new FormData();

    myForm.set("status", status);

    dispatch(deliverOrder(id, myForm));
  };
  const [status, setStatus] = useState("");
  const deleteOrderHandler = (id) => {
    dispatch(deleteOrder(id));
    navigate("/orders")
  };
  useEffect(() => {
    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    if (updateError) {
      alert.error(updateError);
      dispatch(clearErrors());
    }
    if (successDelivered) {
      alert.success("Order Updated Successfully");
      navigate("/orders")

      dispatch({ type: UPDATE_ORDER_RESET });
    }
    dispatch(getOrderDetails(id));
  }, [dispatch, id, successDelivered]);
  return (
    <section className="content-main">
      <div className="content-header">
        <Link to="/orders" className="btn btn-dark text-white">
          Back To Orders
        </Link>
      </div>

      <div className="card">
        <header className="card-header p-3 Header-green">
          <div className="row align-items-center ">
            <div className="col-lg-6 col-md-6">
              <span>
                <i className="far fa-calendar-alt mx-2"></i>
                <b className="text-white">Dec 12 2021</b>
              </span>
              <br />
              <small className="text-white mx-3 ">
                Order ID: {order && order._id}
              </small>
            </div>
            <div className="col-lg-6 col-md-6 ms-auto d-flex justify-content-end align-items-center">
              <select
                className="form-select d-inline-block"
                style={{ maxWidth: "200px" }}
              >
                <option>Change status</option>
                <option>Awaiting payment</option>
                <option>Confirmed</option>
                <option>Shipped</option>
                <option>Delivered</option>
              </select>
              <Link className="btn btn-success ms-2" to="#">
                <i className="fas fa-print"></i>
              </Link>
            </div>
          </div>
        </header>
        <div className="card-body">
          {/* Order info */}
          <OrderDetailInfo order={order} shippingInfo={shippingInfo}
            paymentInfo={paymentInfo} user={user} />
          <div className="row">
            <div className="col-lg-9">
              <div className="table-responsive">
                <OrderDetailProducts order={order} orderItems={orderItems} />
              </div>
            </div>
            {/* Payment Info */}
            <div className="col-lg-3">
              <div className="box shadow-sm bg-light">


                <form
                  className="updateOrderForm"
                  onSubmit={deliverHandler}
                >

                  <div>
                    {/* <AccountTreeIcon /> */}
                    <select onChange={(e) => setStatus(e.target.value)}>
                      <option value="">Choose Category</option>
                      {order && order.orderStatus === "Processing" && (
                        <option value="Shipped">Shipped</option>
                      )}

                      {order && order.orderStatus === "Shipped" && (
                        <option value="Delivered">Delivered</option>
                      )}
                    </select>
                  </div>
                  <br></br>

                  <button
                    id="createProductBtn"
                    type="submit"
                    className="btn btn-dark col-12"
                    disabled={
                      loading ? true : false || status === "" ? true : false
                    }
                  >
                    Process
                  </button>
                </form>
              </div>
              <br></br>
              <div className="col-lg-12">
                <div className="box shadow-sm bg-light">
                  <button

                    className="btn btn-dark col-12"
                    onClick={() => deleteOrderHandler(order._id)
                    }
                  >
                    {/* onClick={() => deleteProductHandler(product._id)} */}
                    Delete Order
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default OrderDetailmain;
