export const orderDetailsReducer = (state = { order: {}, shippingInfo:{},orderItems:[],paymentInfo:{},user:{} }, action) => {

  switch (action.type) {
    case ORDER_DETAILS_REQUEST:
      return {
        loading: true,
      };

    case ORDER_DETAILS_SUCCESS:
     
      return {
        loading: false,
        order: action.payload.order,
        shippingInfo: action.payload.order.shippingInfo,
        orderItems: action.payload.order.orderItems,
        paymentInfo: action.payload.order.paymentInfo,
        user: action.payload.order.user,
      };
    case ORDER_DETAILS_FAIL:
      return {
        loading: false,
        error: action.payload,
      };
    case CLEAR_ERRORS:
      return {
        ...state,
        error: null,
      };

    default:
      return state;
  }
};



// ORDER DELIVERED
export const orderDeliveredReducer = (state = {}, action) => {
  switch (action.type) {
    case ORDER_DELIVERED_REQUEST:
      return { loading: true };
    case ORDER_DELIVERED_SUCCESS:
      return { loading: false, success: true };
    case ORDER_DELIVERED_FAIL:
      return { loading: false, error: action.payload };
    case ORDER_DELIVERED_RESET:
      return {};
    default:
      return state;
  }
};
