import React, { useEffect, useState } from "react";
import "./App.css";
import "./responsive.css";
import "react-toastify/dist/ReactToastify.css";
import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";
import HomeScreen from "./screens/HomeScreen";
import ProductScreen from "./screens/productScreen";
import CategoriesScreen from "./screens/CategoriesScreen";
import OrderScreen from "./screens/OrderScreen";
import OrderDetailScreen from "./screens/OrderDetailScreen";
import AddProduct from "./screens/AddProduct";
import Login from "./screens/LoginScreen";
import UsersScreen from "./screens/UsersScreen";
import ProductEditScreen from "./screens/ProductEditScreen";
import NotFound from "./screens/NotFound";
import { useSelector } from "react-redux";
import axios from "axios";
import { loadUser } from "./actions/userAction";
import store from "./store";
import {Navigate} from "react-router-dom";

function App() {
  const { isAuthenticated, user } = useSelector((state) => state.user);
  const [stripeApiKey, setStripeApiKey] = useState("");

  async function getStripeApiKey() {
    const { data } = await axios.get("/api/v1/stripeapikey");

    setStripeApiKey(data.stripeApiKey);
  }
  useEffect(() => {


    store.dispatch(loadUser());

    getStripeApiKey();
  }, []);
  return (
    <>
      <BrowserRouter>

        <Routes>
          {/* <Route exact path="/" element={<HomeScreen />} /> */}
          <Route exact path="/" element={isAuthenticated ? <HomeScreen/> : <Navigate to="/login"  />}/>
          <Route exact path="/products" element={<ProductScreen />} />

          <Route path="/category" element={<CategoriesScreen/>} />
          <Route path="/orders" element={<OrderScreen/>} />
          <Route path="/order/:id" element={<OrderDetailScreen/>} />
          <Route path="/addproduct" element={<AddProduct/>} />
          <Route path="/users" element={<UsersScreen/>} />
          <Route path="/product/:id" element={<ProductEditScreen/>} />
          <Route path="/login" element={<Login/>} />
          <Route path="*" element={<NotFound/>} />
        </Routes>

      </BrowserRouter>
    </>
  );
}

export default App;
