import React, { useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import OrderDetailmain from "../../components/orders/OrderDetailmain";
import { getOrderDetails } from "../../actions/orderAction";
import { useDispatch, useSelector } from "react-redux";
const OrderDetailProducts = ({ orderItems,order }) => {

  return (
    <>
      {orderItems && orderItems.map((item) => (
        <>
          {item.name}
        </>

      ))}
      <table className="table border table-lg">
        <thead>
          <tr>
            <th style={{ width: "40%" }}>Product</th>
            <th style={{ width: "20%" }}>Unit Price</th>
            <th style={{ width: "20%" }}>Quantity</th>
            <th style={{ width: "20%" }} className="text-end">
              Total
            </th>
          </tr>
        </thead>
        <tbody>
          {orderItems && orderItems.map((item) => (
            <>
              <tr>

                <td>
                  <Link className="itemside" to="#">
                    <div className="left">
                      <img
                        src={item.image}
                        alt="product"
                        style={{ width: "40px", height: "40px" }}
                        className="img-xs"
                      />
                    </div>
                    <div className="info">
                      {item.name}
                    </div>
                  </Link>
                </td>
                <td>{item.price} </td>
                <td>{item.quantity} </td>
                <td className="text-end"> Rs {item.price * item.quantity}</td>

              </tr>
            </>

          ))}
          <tr>
            <td colSpan="4">
              <article className="float-end">
                <dl className="dlist">
                  <dt>Subtotal:</dt> <dd>RS {order && order.itemsPrice}</dd>
                </dl>
                <dl className="dlist">
                  <dt>Shipping cost:</dt> <dd>RS {order && order.shippingPrice}</dd>
                </dl>
                <dl className="dlist">
                  <dt>GST</dt>
                  <dd>
                    <b className="h5">RS {order && order.taxPrice}</b>
                  </dd>
                </dl>
                <dl className="dlist">
                  <dt>Grand total:</dt>
                  <dd>
                    <b className="h5">RS {order && order.totalPrice}</b>
                  </dd>
                </dl>
                <dl className="dlist">
                  <dt className="text-muted">Status:</dt>
                  <dd>
                    <span className="badge rounded-pill alert alert-success text-success">
                      Payment done
                    </span>
                  </dd>
                </dl>
              </article>
            </td>
          </tr>
        </tbody>
      </table>
    </>
  );
};

export default OrderDetailProducts;
